.uni-float {
  float: left;
  margin: 0 20px 20px 0;
}

.uni-float--whole {
  width: calc(100% - 20px);
}

@media (min-width: 1000px) {
  .uni-float--half, .uni-float--third {
    width: calc(50% - 20px);
  }
}

@media (min-width: 1500px) {
  .uni-float--third {
    width: calc(33.3333% - 20px);
  }
}

@media (min-width: 500px) {
  .uni-float--quarter {
    width: calc(50% - 20px);
